@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App-header{width:100%;height:128px;top:0;color:white;margin-right:auto;margin-left:auto;display:flex;flex-direction:row;justify-content:space-between;background-color:#1a237e}.header-left{display:flex;flex-direction:row;text-align:left}@media (max-width: 768px){.header-right{display:none}}.product-card{margin:10px;display:flex;flex-flow:column wrap;align-items:stretch;align-content:flex-start;border-radius:5%;box-shadow:2px 4px 5px 0px grey}@media (max-width: 480px){.product-card{max-width:100%}}@media (max-width: 480px) and (orientation: landscape){.product-card{max-width:100%}}@media (min-width: 480px) and (orientation: portrait){.product-card{max-width:33.33%}}@media (min-width: 768px) and (orientation: landscape){.product-card{max-width:33.33%}}@media (min-width: 1024px) and (orientation: portrait){.product-card{max-width:25%}}@media (min-width: 768px) and (orientation: landscape){.product-card{max-width:25%}}.product-image-container{position:relative;min-height:15em}.product-image{min-height:15em;max-height:20em;background-size:cover;background-position:50% 50%;background-repeat:no-repeat;margin-right:auto;margin-left:auto;width:100%;border-top-right-radius:5%;border-top-left-radius:5%}.product-label{position:absolute;top:15px;left:15px}.product-rating{position:absolute;bottom:15px;left:15px}.product-favourite{position:absolute;bottom:15px;right:15px;opacity:0;-webkit-transition:.5s ease;transition:.5s ease}.product-image-container:hover .product-favourite{opacity:1}.product-favourite:hover{fill:1}.badge{color:white;border-radius:7%}.badge-red{background-color:#df171e;padding:5px}.product-info{display:flex;flex-direction:column;padding:10px}.product-style{display:flex;align-items:center}.product-origin{display:flex;align-items:center}.product-abv{display:flex;align-items:center}.style-icon{padding-right:1rem}.product-price{margin-top:auto;border-top:1px solid;display:flex;flex-direction:column;padding:10px}.product-form{display:flex;align-items:stretch}.product-quantity{width:25%}.atb-btn{width:75%}html{box-sizing:border-box;font-family:"Roboto","normal"}@media screen and (max-width: 480px){.main-section{height:100%;overflow:auto;overflow-x:hidden;padding:1rem}}@media screen and (min-width: 1280px){.main-section{width:1280px;margin-left:auto;margin-right:auto}}.App-body{height:100%;width:100%}.products{display:flex;flex-flow:row wrap;justify-content:center;align-items:stretch;align-content:flex-start}

